<template>
    <div style="border:1px solid #cccccc; padding:20px;">
        <h3></h3>

        <button @click="handlePage"></button>
        <button @click="handleDeleteBatch"></button>
        <button @click="handleUpdateBatch"></button>
        {{state.chk}}

        <table border="1">
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>



            </tr>
            

            <tr v-for="(item, idx) in state.items" :key="item">
            <td><input type="checkbox" v-model="state.chk" :value="item._id"/></td>
            <td><button @click="handleDetailPage(item._id)">{{item._id}}</button></td>
            <td><img :src="item.imageUrl" style="width:50px; height:50px" />
                <input type="file" @change="handleImage($event, idx)"/></td>
            <td><input type="text" v-model="item.name" style="width:60px"/></td>
            <td><input type="text" v-model="item.price" style="width:60px"/></td>
            <td><input type="text" v-model="item.quantity" style="width:60px"/></td>
            <td><input type="text" v-model="item.content" style="width:60px"/></td>
            <td>{{item.regdate}}</td>
            <td>
            <button @click="handleDeleteAction([item._id])"></button>
            <button @click="handleUpdateAction(idx)"></button>
            </td>

            </tr>

        </table>
    </div>
</template>

<script>
import { reactive, onMounted } from 'vue'
import axios from 'axios';
import { useRouter } from 'vue-router'; //   
export default {
    setup () {
        const router = useRouter();  //  
        const state = reactive({
            chk : [],
            token : sessionStorage.getItem("TOKEN")
        });

        const handleDeleteBatch = async() => {
                // state.chk = [1,2,3]
                await handleDeleteAction(state.chk);
                state.chk = [];
        }

        //  
        const handleDetailPage = (code) => {
            router.push({name:'Menu1Detail',query: {code:code}});
        }

        // 
        const handleUpdateBatch = async () => {
            let arr = [];
            // ex)  (1,2,3,4,5,6,7,) 1    1 => 3 
            //  7 =>  21 
            for(let i =0;i<state.items.length;i++){
                // ex)  (3,4,6)
                for(let j=0; j<state.chk.length;j++){
                    if(state.items[i]._id === state.chk[j]){
                        // 
                        console.log(state.items[i]._id, state.chk[j]);
                        // arr    
                        arr.push( state.items[i]);

                    }
                }

            }
             const url = `/seller/update`;
            const headers = {"Content-Type":"multipart/formdata",
                             "token" : state.token};
            // {code : 1016, title:'a'}
            // {code :[1016], title:['a']}
            const body = new FormData();
            // arr     ()
            for(let i=0; i<arr.length; i++){
                body.append("image", arr[i].image); //X
                 body.append("code",  arr[i]._id );
                 body.append("title", arr[i].name);
                 body.append("price", arr[i].price);
                 body.append("quantity", arr[i].quantity);
                 body.append("content", arr[i].content);
            }
            const response = await axios.put(url,body,{headers:headers});
            console.log(response.data);
            //state.items => [{},{},{}]
            if(response.data.status === 200){
                await handleLoadData();
                state.chk = [];
            }
        }
        // 
        const handleUpdateAction = async(idx) => {
            const url = `/seller/update`;
            const headers = {"Content-Type":"multipart/formdata",
                             "token" : state.token};
            // {code : 1016, title:'a'}
            // {code :[1016], title:['a']}
            const body = new FormData();
           body.append("image", state.items[idx].image); //X
            body.append("code",  state.items[idx]._id );
            body.append("title", state.items[idx].name);
            body.append("price", state.items[idx].price);
            body.append("quantity", state.items[idx].quantity);
            body.append("content", state.items[idx].content);

            const response = await axios.put(url,body,{headers:headers});
            console.log(response.data);
            //state.items => [{},{},{}]
            if(response.data.status === 200){
                await handleLoadData();
                state.chk = [];
            }

        }

            // 
            const handleDeleteAction = async(code) => {
                if(confirm('??')) {
                    const url = `/seller/delete`;
                    const headers = {
                        "Content-Type":"application/json",
                        "token" : state.token
                    };
                    // { code : [ 1 ] }
                    const body = {code:code};
    
                    const response = await axios.delete(url, 
                            {headers:headers, data:body});
                    console.log(response.data);
                    if(response.data.status === 200) {
                        await handleLoadData();
                        state.chk = [];
                    }
                }
            } 
        const handlePage =  () => {
            router.push({name:'Menu1Insert'})
        };

        const handleImage = (e, idx) => {
            console.log(e);   //   
            console.log(idx); // 
            if(e.target.files[0]){
                state.items[idx].image = e.target.files[0];

            }   
            else {
                state.items[idx].image = ''
            } 
        }

      


        const handleLoadData = async() => {
             const url = `/seller/selectlist`;
            const headers = {"Content-Type":"application/json",
                             "token"       :state.token};
            const response = await axios.get(url, {headers});
            console.log(response.data);
            if(response.data.status === 200){
                state.items = response.data.result;
            }
        }
        
        // 
        onMounted( async()=> {
           await handleLoadData();
            
        });



        return {state,handlePage,
        handleDeleteAction,
        handleLoadData,
        handleImage,
        handleUpdateAction,
        handleDeleteBatch,
        handleUpdateBatch,
        handleDetailPage}
    }
}
</script>

<style lang="scss" scoped>

</style>