<template>
    <div>
    <h3>src/components/Menu2.vue</h3>
     : <input type="password" v-model="state.pw"> <br/>
     : <input type="password" v-model="state.pw1"><br/>
      : <input type="password" v-model="state.pw2"><br/>
    <button @click="handleUpdateAction"></button>
    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';

export default {
    setup () {
        const state = reactive({
            pw : '',
            pw1 : '',
            pw2 : '',
            token : sessionStorage.getItem("TOKEN"),
        });

       const handleUpdateAction = async () => {
            if(state.token !== null){

            const url = `/member/updatepw`;
            const headers = {"Content-Type":"application/json",
                             "token":state.token
            };
            const body = {
                password : state.pw,
                password1 : state.pw1
            }
            const response = await axios.put(url,body,{headers});
            console.log(response.data);
            if(response.data.status === 200){
                alert(' ')
            }
            if(state.pw === ''){
                alert(' ');
                return false;
            }
            if(state.pw1 === ''){
                alert(' ');
                return false;
            }
            if(state.pw2 === ''){
                alert(' ');
                return false;
            }
            if(state.pw1 !== state.pw2){
                alert('  ');
                return false;
            }
            if(response.data.status === 0){
                alert('  ')
                return false;
            }

            }
        }
        

        return {state,handleUpdateAction}
    }
}
</script>

