<template>
    <div>
        <h3>src/components/Admin.vue</h3>
        {{menu}}
        <button @click="handlemenu(1)">1</button>
        <button @click="handlemenu(2)">2</button>
        <button @click="handlemenu(3)">3</button>
        <button @click="handleCount">1 </button>
        <hr/>
        <!-- // handleCount  -->
        <menu-1 v-if="menu.code === 1"
         title="1" :count="count.value" @handleCount="handleCount"></menu-1>
        <menu-2 v-if="menu.code === 2"
         title="2" :count="count.value" @handleCount="handleCount"></menu-2>
        <menu-3 v-if="menu.code === 3"
         title="3" :count="count.value" @handleCount="handleCount"></menu-3>
    </div>
</template>

<script>
import Menu1 from './admin/Menu1.vue';
import Menu2 from './admin/Menu2.vue';
import Menu3 from './admin/Menu3.vue';

import { reactive } from '@vue/reactivity'
export default {
    components : {
        Menu1,Menu2,Menu3
    },
    setup () {
        // state 
        // menu.code
        const menu = reactive({
            code : 1
        });
        // count.value 
        const count = reactive(
            {value : 1}
        )

        //handleMenu 
        const handlemenu = (idx) => {
            console.log(idx);
            menu.code = idx;
        }
        //count.value 1 
        const handleCount = () => {
            count.value ++;
        }
        

        return {menu,count,handlemenu,handleCount}
    }
}
</script>

