<template>
    <div>
        <h3>Login.vue</h3>
        {{state}}
                <el-form>
                <el-form-item label="" label-width="120px" >
                    <el-input v-model="state.userid"  size="mini" placeholder="" style="width:192px"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="120px">
                    <el-input v-model="state.userpw" size="mini" placeholder="" style="width:192px"></el-input>
                </el-form-item>
                 <el-button value="" @click="handleLogin" style="margin-left:120px"></el-button>
                </el-form>
        
    </div>
</template>

<script>
import { reactive } from 'vue'
import axios from 'axios';
import { useRouter } from 'vue-router';
import { useStore} from 'vuex'

export default {
    // ver 3.0    
    setup () { // this   
        const store = useStore();
        const router = useRouter();
        const state = reactive({
            userid : 'c@com',
            userpw : 'c'
        });

        const handleLogin = async() =>{
            const url = `/member/select`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                email : state.userid,
                password : state.userpw
            };
            const response = await axios.post(url, body,{headers});
            console.log(response.data.token);
            
            
            if(response.data.status === 200){
                alert(' ');
                console.log('login.vue=>',response.data);
                //  ()    ()    
                sessionStorage.setItem("TOKEN", response.data.token); 
               
                // 3.  
                store.commit("setLogged",true);
                // acions  store/state 
                store.dispatch("handleData",{})
                
                const curl = sessionStorage.getItem("CURL");
                if(curl === null){
                    // 1.  
                    router.push({name:'Home'})
                    // 2.  
                   store.commit("setMenu","/");

                    }
                else {  //   
                    // string => object 
                    const query  =JSON.parse(sessionStorage.getItem("CRUL_QUERY"))
                    const params =JSON.parse(sessionStorage.getItem("CRUL_PARAMS"))
                    router.push({name:curl, query:query, params:params})
                }

                // App.vue    
                //    
                
                
            }
            if(response.data.status !== 200){
                alert('   ')
            }
          
        };

        return {state,handleLogin}
    }
}


// state  ver 2.0
// data(){
//     return{


//     }

</script>

