<template>
    <div class="style1">
        <h3>src/components/Join.vue</h3> <br/>
        {{state}} <hr/>
         : 
        <el-input v-model="state.userid"  size="mini" style="width:300px" @keyup="handleEmailcheck" ref="userid" /><br/>
        <!-- <input type="text" ref="userid" v-model="state.userid" @keyup="handleEmailcheck"/><br/> -->
        <div>{{state.useremailcheck}}</div><br/>
         : <input type="password"  ref="userpw" v-model="state.userpw"/><br/>
          : <input type="password"  ref="userpw1" v-model="state.userpw1"/><br/>
         : <input type="text"  ref="username" v-model="state.username"/><br/>
        
         : 
        <select v-model="state.userrole">
            <option value="CUSTOMER"></option>
            <option value="SELLER"></option>
        </select> <br/>


        <button @click="handleJoin"></button>
    </div>
</template>

<script>
import { reactive, ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
export default { 
    setup () {
        const router = useRouter();

        // Low  :   :  /
        const state = reactive({
            userid      : '',
            userpw      : '',
            userpw1     : '',
            username    : '' ,
            useremailcheck : '',
            userrole     : 'CUSTOMER',
        });


        // v-model   
        const userid   = ref(null); //  bbb  
        const userpw   = ref(null);
        const userpw1  = ref(null);
        const username = ref(null);
        
        //    
        const validEmail = (email) => {
        //  
        var re = /[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]*$/i;
        return re.test(email);
        };


        const handleEmailcheck = async() => {
            //        
            if(validEmail(state.userid)){
            console.log(state.userid);
            const url = `/member/emailcheck?email=${state.userid}`;
            const headers = {"Content-Type":"application/json"};
                const response = await axios.get(url, {headers});
                console.log(response.data);
                if(response.data.status === 200){
                    if(response.data.result === 1){
                        state.useremailcheck='';
                    }
                    else{
                        state.useremailcheck=''
                    }

            }
        }
        else{
            state.useremailcheck='';
        }
        }

        //function handleJoin() {}
        const handleJoin = async() => {
            
            if(state.useremailcheck !== ''){
                alert('  ')
                userid.value.focus;
                return false;
            }
            if(state.userid === ''){
                alert(' ')
                userid.value.focus();
                return false;
            }
            
            if(state.userpw === ''){
                alert(' ')
                userpw.value.focus();
                return false;
            }

            if(state.userpw1 === ''){
                alert(' ')
                userpw1.value.focus();
                return false;
            }
            if(state.userpw !== state.userpw1){
                alert('  ')
                userpw1.value.focus();
                return false;
            }
            
            if(state.username === ''){
                alert(' ')
                username.value.focus();
                return false;
            }
            //      

            const url = `/member/insert`;
            const headers = {"Content-Type":"application/json"};
            const body = {
                email    : state.userid, 
                password : state.userpw, 
                name     : state.username,
                role     : state.userrole,
                };
            const response = await axios.post(url, body, {headers});
                if(response.data.status === 200){
                    alert('');
                    router.push({name:'Home'});
                }
            

        }
       

        return {state, userid, userpw, userpw1,
         username, handleJoin,handleEmailcheck}
    }
}
</script>

