<template>
    <div>
        <h3> : src/components/ItemContents.vue</h3>
         <router-link to="/"></router-link>
        {{state}}
        <div v-if="state.item">
             <img :src="state.item.imageUrl" style="width:200px"/> <br/>
               : {{state.item._id}}<br />
             : {{state.item.name}}<br />
               : {{state.item.price}}<br />
               : {{state.item.quantity}}<br />
               : {{state.item.content}}<br />

            <select v-model="state.item.ordercnt">
                <option v-for="no in 100" :key="no" style="margin:150px;"><br/>
                {{no}}
                </option> <br/>
            </select>
            <button @click="handleOrderAction"></button>
            <button @click="handleCartAction"></button>
            <button @click="handleCartList"></button>
            <!-- <div v-for="tmp in state.item.subImage" :key="tmp" style="display:inline-block">
                <img :src="tmp.subimageUrl" style="width:150px;height:150px" />
                </div>  -->
        </div>
    </div>
</template>

<script>
import { reactive, onMounted } from 'vue';
import { useRoute } from 'vue-router'; //   
import { useRouter} from 'vue-router'; //   
import axios from 'axios';
export default {
    setup () {
        const route = useRoute();
        const router = useRouter();
        const state = reactive({
            code : route.query.code,
        });

        const handleLoadData = async() => {
            const url = `/shop/selectone?code=${state.code}`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url, {headers});
            if(response.data.status === 200){
                state.item = response.data.result;
            }
                console.log(response.data.result);
            
        }

        const handleCartList = async() => {
            const url = `/shop/selectcart`;
            const headers = {"Content-Type":"application/json"};
            const response = await axios.get(url, {headers});
               console.log(response.data);
        }

         const handleOrderAction = async() => {
            router.push({name : "Order" ,
            query : {code:state.item._id, 
                    cnt :state.item.ordercnt }});
         }

       const handleCartAction = async() => {
            const url = `/shop/insertcart`;
            const headers = {"Content-Type":"application/json"};
            // , ,    
            const body = {
                code: state.item._id, 
                cnt : state.item.ordercnt
            }
            const response = await axios.post(url,body,{headers})
            console.log(response.data);
        }

        //
        onMounted( async()=> {
           await handleLoadData();
            
        });
        
        //  OnMounted 
        // /seller/selectone?code=111   .

        return {state,handleOrderAction,handleCartAction,handleCartList}
    }
}
</script>

